@import 'styles/variables';
@import 'styles/mixins';

html[data-theme='dark'] {
  .container {
    color: $white;
    background-color: $darkBackgroundColor2 !important;

    .appInfo {
      h1 {
        &.appNameColor1 {
          color: $darkTextColor1;
        }
        &.appNameColor2 {
          color: $primaryColor;

          &.primaryColor2 {
            color: $primaryColor2;
          }
          &.primaryColor3 {
            color: $primaryColor3;
          }
        }
      }

      .menuIcon {
        filter: invert(43%) sepia(13%) saturate(491%) hue-rotate(171deg)
          brightness(130%) contrast(84%);
      }
    }

    .menu {
      .item {
        color: $darkTextColor1;

        &:hover {
          span {
            color: $darkTextColor2;
          }
        }

        .group {
          background-color: $darkBackgroundColor4;

          .groupItem {
            .subTitle {
              color: $darkTextColor2;
            }
          }
        }
      }
    }
  }
}

body[data-dark-background='true'] {
  .container {
    color: $white;

    &.backgroundColor1 {
      background-color: $backgroundColor1;
    }
    &.backgroundColor2 {
      background-color: $backgroundColor2;
    }
    &.backgroundColor3 {
      background-color: $backgroundColor3;
    }
    &.backgroundColor4 {
      background-color: $backgroundColor4;
    }
    &.backgroundColor5 {
      background-color: $backgroundColor5;
    }
    &.backgroundColor6 {
      background-color: transparent;
    }

    .appInfo {
      .menuIcon {
        filter: invert(43%) sepia(13%) saturate(491%) hue-rotate(171deg)
          brightness(390%) contrast(84%);
      }
    }

    .menu {
      .item {
        color: $white;

        &:hover {
          span {
            color: $darkTextColor1;
          }
        }

        .group {
          .groupItem {
            .title {
              color: $black;
            }
            .subTitle {
              color: $darkTextColor3;
            }
          }
        }

        .itemContainer {
          .toggleIcon {
            filter: invert(43%) sepia(13%) saturate(491%) hue-rotate(171deg)
              brightness(207%) contrast(84%);
            opacity: 0.6;
          }
        }
      }
    }

    .buttons {
      img {
        filter: invert(43%) sepia(13%) saturate(491%) hue-rotate(171deg)
          brightness(290%) contrast(84%);

        &:hover {
          filter: invert(43%) sepia(13%) saturate(491%) hue-rotate(171deg)
            brightness(190%) contrast(84%);
        }
      }
    }
  }
}

.container {
  display: flex;
  align-items: center;
  padding: 8px 64px;

  &.headerFixed2 {
    position: fixed;
    top: 0;
    z-index: 9999;
    width: calc(100% - 128px);
    min-height: 61.44px;
    @include boxShadow15b;
  }

  &.backgroundColor1 {
    background-color: $backgroundColor1;

    &.backgroundNoise2 {
      background-image: url('../../assets/images/background-pattern-low.png');
    }
    &.backgroundNoise3 {
      background-image: url('../../assets/images/background-pattern-high.png');
    }
  }
  &.backgroundColor2 {
    background-color: $backgroundColor2;
  }
  &.backgroundColor3 {
    background-color: $backgroundColor3;
  }
  &.backgroundColor4 {
    background-color: $backgroundColor4;
  }
  &.backgroundColor5 {
    background-color: $backgroundColor5;
  }
  &.backgroundColor6 {
    background-color: transparent;
  }

  .appInfo {
    display: flex;
    margin-left: 32px;

    .menuIcon {
      display: none;
      width: 32px;
      margin-left: 16px;
    }

    .logo {
      width: 32px;
      margin-left: 8px;

      &.appLogoSize1 {
        width: 24px;
      }
      &.appLogoSize2 {
        width: 32px;
      }
      &.appLogoSize3 {
        width: 36px;
      }
    }

    h1 {
      font-size: 20px;
      font-weight: 600;

      &.appNameSize1 {
        font-size: 16px;
      }
      &.appNameSize2 {
        font-size: 20px;
      }
      &.appNameSize3 {
        font-size: 24px;
      }

      &.appNameColor1 {
        color: $textColor1;
      }
      &.appNameColor2 {
        color: $primaryColor;

        &.primaryColor2 {
          color: $primaryColor2;
        }
        &.primaryColor3 {
          color: $primaryColor3;
        }
      }
    }
  }

  .menu {
    flex: 1;
    display: flex;

    &.menuPosition1 {
      justify-content: initial;
    }
    &.menuPosition2 {
      justify-content: center;
    }

    .item {
      color: $textColor1;
      font-size: 12px;
      margin: 0 12px;
      position: relative;
      cursor: pointer;

      &.menuItemFontSize1 {
        font-size: 12px;
      }
      &.menuItemFontSize2 {
        font-size: 13px;
      }
      &.menuItemFontSize3 {
        font-size: 14px;
      }

      &:hover {
        span {
          color: $textColorFade;
        }

        .itemContainer {
          .toggleIcon {
            filter: invert(43%) sepia(13%) saturate(491%) hue-rotate(171deg)
              brightness(97%) contrast(84%);
            opacity: 0.6;
          }
        }
      }

      .itemContainer {
        .toggleIcon {
          margin-right: 4px;
        }
      }

      .group {
        background-color: $white;
        display: flex;
        flex-direction: row;
        position: absolute;
        z-index: 2;
        border-radius: 4px;
        visibility: hidden;
        opacity: 0;
        @include boxShadow15b;

        &.menuGroupPosition1 {
          left: 0;

          &.visible {
            transform: translate(calc(-100% + 50px), 10px);
          }
        }
        &.menuGroupPosition2 {
          left: 50%;
        }

        &.menuGroupType1 {
          top: calc(100%);
          padding: 4px 0;
          white-space: nowrap;
        }
        &.menuGroupType2,
        &.menuGroupType3,
        &.menuGroupType4 {
          top: calc(100%);
          padding: 4px 0;
          white-space: nowrap;
          min-width: 100px;

          .groupItem {
            justify-content: flex-start;
            padding: 0 16px 0 32px;
          }
        }
        &.menuGroupType3,
        &.menuGroupType4,
        &.menuGroupType5 {
          .groupItem {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: flex-start;
            padding: 8px 16px 8px 32px;

            .title {
              white-space: nowrap;
              min-width: 140px;
            }

            .subTitle {
              color: $textColor2;
              font-size: 10px;
            }
          }
        }

        &.menuGroupAnimation1 {
          transform: translate(-50%, 20px);
          transition: all 0.3s;
        }
        &.menuGroupAnimation2 {
          transform: translateX(-50%);
          transition: 0.3s;
        }

        &.visible {
          visibility: visible;
          transform: translate(-50%, 10px);
          opacity: 1;
        }

        span {
          color: $textColor1;
          font-size: 12px;
          margin: 0 8px;
          cursor: pointer;
        }

        .groupItem {
          display: flex;
          justify-content: center;
          align-items: center;
          padding: 0 32px;
          min-height: 30px;

          &:hover {
            background-color: rgba(0, 0, 0, 0.03);
          }
        }

        .imageContainer {
          display: flex;
          justify-content: center;
          align-items: center;
          width: 120px;
          padding: 32px;
        }
      }
    }
  }

  .buttons {
    display: flex;
    align-items: center;

    &.headerButtonGroup2 {
      img {
        margin-left: 16px;
      }
    }

    & > * {
      margin-left: 8px;

      &:last-child {
        margin-left: 0;
      }
    }

    &.headerButtonOrder2 {
      flex-direction: row-reverse;

      & > * {
        margin-left: 8px;

        &:first-child {
          margin-left: 0;
        }
        &:last-child {
          margin-left: 8px;
        }
      }
    }

    img {
      width: 24px;
      height: 24px;
      filter: invert(43%) sepia(13%) saturate(491%) hue-rotate(171deg)
        brightness(90%) contrast(84%);
      cursor: pointer;

      &:hover {
        filter: invert(43%) sepia(13%) saturate(491%) hue-rotate(171deg)
          brightness(130%) contrast(84%);
      }
    }
  }
}

@media screen and (max-width: 1080px) {
  .container {
    padding: 8px 32px;

    &.headerFixed2 {
      width: calc(100% - 64px);
    }
  }
}

@media screen and (max-width: 860px) {
  .container {
    justify-content: space-between;

    .appInfo {
      .menuIcon {
        display: block;
      }
    }

    .menu {
      display: none;
    }
  }
}

@media screen and (max-width: 720px) {
  .container {
    .appInfo {
      margin-left: 0;

      h1 {
        font-size: 18px;
        font-weight: 600;

        &.appNameSize1 {
          font-size: 14px;
        }
        &.appNameSize2 {
          font-size: 16px;
        }
        &.appNameSize3 {
          font-size: 20px;
        }
      }

      .logo {
        &.appLogoSize1 {
          width: 22px;
        }
        &.appLogoSize2 {
          width: 24px;
        }
        &.appLogoSize3 {
          width: 28px;
        }
      }
    }
  }
}

@media screen and (max-width: 540px) {
  .container {
    padding: 8px 16px;

    &.headerFixed2 {
      width: calc(100% - 32px);
    }
  }
}
