@import 'styles/variables';

@mixin boxShadow5b {
  box-shadow: 0 2px 5px $shadowColor;
}
@mixin boxShadow10b {
  box-shadow: 0 2px 10px $shadowColor;
}
@mixin boxShadow15b {
  box-shadow: 0 2px 15px $shadowColor;
}
@mixin boxShadow30b {
  box-shadow: 0 2px 30px $shadowColor;
}
@mixin boxShadow60b {
  box-shadow: 0 2px 60px $shadowColor;
}
@mixin boxShadow120b {
  box-shadow: 0 2px 120px $shadowColor;
}
@mixin boxShadowDark15b {
  box-shadow: 0 2px 15px $shadowColorDark;
}
@mixin boxShadowBottom15b {
  box-shadow: 0 12px 15px $shadowColor;
}
