@font-face {
  font-family: 'iransansxv';
  src: url('/assets/fonts/IranSansX/IRANSansXV.woff') format('woff-variations'),
    /* will be the standard and works in Safari now */
      url('/assets/fonts/IranSansX/IRANSansXV.woff') format('woff');
  /* for the other supporting browsers */
  font-weight: 100 900;
  font-display: fallback;
}

@font-face {
  font-family: iransansx;
  src: url('/assets/fonts/IranSansX/staticfonts/IRANSansX-Regular.woff')
    format('woff');
}

@font-face {
  font-family: iransansx;
  src: url('/assets/fonts/IranSansX/staticfonts/IRANSansX-Bold.woff')
    format('woff');
  font-weight: 700;
}
