@import 'styles/variables';

html[data-theme='dark'],
body[data-dark-background='true'] {
  .container {
    color: $darkTextColor1;
  }
}

.container {
  height: 100%;

  &.headerFixed2 {
    .header {
      display: none;
    }

    .content {
      margin-top: 77.44px;
    }
  }

  .header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 8px 32px;

    .appInfo {
      display: flex;
      align-items: center;
      margin-left: 32px;

      .menuIcon {
        display: none;
        width: 32px;
        margin-left: 16px;
      }

      .logo {
        width: 32px;
        margin-left: 8px;

        &.appLogoSize1 {
          width: 24px;
        }
        &.appLogoSize2 {
          width: 32px;
        }
        &.appLogoSize3 {
          width: 36px;
        }
      }

      h1 {
        font-size: 20px;
        font-weight: 600;

        &.appNameSize1 {
          font-size: 16px;
        }
        &.appNameSize2 {
          font-size: 20px;
        }
        &.appNameSize3 {
          font-size: 24px;
        }

        &.appNameColor1 {
          color: $textColor1;
        }
        &.appNameColor2 {
          color: $primaryColor;

          &.primaryColor2 {
            color: $primaryColor2;
          }
          &.primaryColor3 {
            color: $primaryColor3;
          }
        }
      }
    }
  }

  .content {
    flex: 1;
    overflow: auto;

    .menu {
      .menuItem {
        font-size: 12px;
        padding: 24px 32px;
        border-bottom: 1px solid #efefef;

        &.mobileMenuItemBordered1 {
          border-width: 0;
        }

        &.mobileMenuItemSpacing1 {
          padding: 16px 32px;
        }
        &.mobileMenuItemSpacing2 {
          padding: 24px 32px;
        }
        &.mobileMenuItemSpacing3 {
          padding: 32px 32px;
        }
      }
    }
  }

  .footer {
    display: flex;
    justify-content: flex-end;
    padding: 32px;

    .button {
      margin-left: 8px;

      &:last-child {
        margin-left: 0;
      }
    }
  }
}
