@import 'styles/variables';
@import 'styles/mixins';

html[data-theme='dark'] {
  .container {
    &:hover {
      &.transparent {
        color: $darkTextColor2;
      }
    }
  }
}

body[data-dark-background='true'] {
  .container {
    &:hover {
      &.transparent {
        color: $darkTextColor1;
      }
    }
  }
}

.container {
  position: relative;
  top: 0;
  padding: 8px 16px;
  border-radius: 4px;
  font-size: 12px;
  white-space: nowrap;
  cursor: pointer;

  &.buttonRadius1 {
    border-radius: 4px;
  }
  &.buttonRadius2 {
    border-radius: 6px;
  }
  &.buttonRadius3 {
    border-radius: 8px;
  }
  &.buttonRadius4 {
    border-radius: 10px;
  }

  &.buttonPadding1 {
    padding: 8px 16px;
  }
  &.buttonPadding2 {
    padding: 10px 18px;
  }
  &.buttonPadding3 {
    padding: 12px 20px;
  }

  &.buttonFontSize1 {
    font-size: 12px;
  }
  &.buttonFontSize2 {
    font-size: 13px;
  }
  &.buttonFontSize3 {
    font-size: 14px;
  }

  &:hover {
    &.buttonAnimation1 {
      top: -2px;
      transition: top 0.2s;
    }

    &:not(.transparent) {
      @include boxShadowBottom15b;
    }

    &.transparent {
      color: $textColorFade;
    }
  }

  &.primary {
    background-color: $primaryColor;
    color: $white;

    &.primaryColor2 {
      background-color: $primaryColor2;
    }
    &.primaryColor3 {
      background-color: $primaryColor3;
    }
  }
}
