@import 'styles/variables';
@import 'styles/mixins';

html[data-theme='dark'] {
  .container {
    color: $white;
    background-color: $darkBackgroundColor2;

    .heading {
      .title {
        h2 {
          color: $darkTextColor2;
        }

        .jumpToSection {
          color: $darkTextColor2;

          &:hover {
            color: $darkTextColor3;
          }
        }
      }
    }

    .marketsContainer {
      .tableContainer {
        color: $white !important;
        background-color: $darkBackgroundColor4;

        table {
          tbody {
            tr {
              td {
                .cellContainer {
                  .description {
                    .subTitle {
                      color: $darkTextColor2;
                    }
                  }
                }
              }
            }
          }
        }
      }

      .footer {
        .more {
          color: $darkTextColor2;

          &:hover {
            color: $darkTextColor3;
          }
        }
      }
    }

    .exchangeContainer {
      .description {
        .texts {
          h3 {
            color: $darkTextColor2;
          }
        }
      }

      .content {
        .calculatorContainer {
          background-color: $darkBackgroundColor4;

          form {
            .inputContainer {
              input {
                color: $black;
                background-color: lightgray;
              }

              label {
                color: $textColor2;
              }
            }
          }
        }
      }
    }

    .informationContainer {
      .context {
        h3 {
          color: $darkTextColor2;
        }
      }
    }
  }
}

body[data-dark-background='true'] {
  .container {
    color: $white;

    .heading {
      .title {
        h2 {
          color: $darkTextColor1;
        }

        .jumpToSection {
          color: $darkTextColor1;

          &:hover {
            color: $darkTextColor2;
          }
        }
      }
    }

    .marketsContainer {
      .header {
        .tabs {
          .tab {
            color: $darkTextColor2;

            &.active {
              color: $primaryColor;
            }
          }
        }
      }

      .tableContainer {
        color: $black;
      }

      .footer {
        .more {
          color: $darkTextColor1;

          &:hover {
            color: $darkTextColor2;
          }
        }
      }
    }

    .exchangeContainer {
      .description {
        .texts {
          h3 {
            color: $darkTextColor1;
          }
        }
      }

      .content {
        .calculatorContainer {
          form {
            .inputContainer {
              label {
                color: $textColor2;
              }
            }
          }
        }
      }
    }

    .informationContainer {
      .context {
        h3 {
          color: $darkTextColor1;
        }
      }
    }
  }
}

.container {
  min-height: 100vh;

  &.headerFixed2 {
    padding-top: 80px;
  }

  &.backgroundColor1 {
    background-color: transparent;
  }
  &.backgroundColor2 {
    background-color: $backgroundColor2;
  }
  &.backgroundColor3 {
    background-color: $backgroundColor3;
  }
  &.backgroundColor4 {
    background-color: $backgroundColor4;
  }
  &.backgroundColor5 {
    background-color: $backgroundColor5;
  }
  &.backgroundColor6 {
    background-color: transparent;
  }

  .heading {
    display: flex;
    align-items: center;
    padding: 0 64px;
    min-height: calc(100vh - 300px);

    .title {
      flex: 1;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;

      .header {
        text-align: right;

        h1 {
          margin-bottom: 8px;
          font-size: 30px;
          font-weight: 500;
        }

        h2 {
          color: $textColor2;
          margin-top: 0;
          margin-bottom: 24px;
          font-size: 16px;
          font-weight: 500;
        }

        &.firstViewFontSize1 {
          h1 {
            font-size: 30px;
          }
          h2 {
            font-size: 16px;
          }
        }
        &.firstViewFontSize2 {
          h1 {
            font-size: 32px;
          }
          h2 {
            font-size: 18px;
          }
        }
        &.firstViewFontSize3 {
          h1 {
            font-size: 34px;
          }
          h2 {
            font-size: 20px;
          }
        }

        .button {
          width: fit-content;
          margin-bottom: 8px;
        }

        .inputGroup {
          display: flex;
          margin-bottom: 8px;

          input[type='text'] {
            border: 1px solid $primaryColor;
            border-radius: 0 4px 4px 0;
            padding: 0 16px 0 0;
            margin-left: -1px;
            font-family: iransansx;
            font-size: 12px;
            outline: none;
            min-width: 180px;

            &.buttonRadius1 {
              border-radius: 0 4px 4px 0;
            }
            &.buttonRadius2 {
              border-radius: 0 6px 6px 0;
            }
            &.buttonRadius3 {
              border-radius: 0 8px 8px 0;
            }
            &.buttonRadius4 {
              border-radius: 0 10px 10px 0;
            }

            &.primaryColor2 {
              border-color: $primaryColor2;
            }
            &.primaryColor3 {
              border-color: $primaryColor3;
            }
          }

          .inputGroupButton {
            border-radius: 4px 0 0 4px;

            &.buttonRadius1 {
              border-radius: 4px 0 0 4px;
            }
            &.buttonRadius2 {
              border-radius: 6px 0 0 6px;
            }
            &.buttonRadius3 {
              border-radius: 8px 0 0 8px;
            }
            &.buttonRadius4 {
              border-radius: 10px 0 0 10px;
            }

            &:hover {
              top: initial;
              transition: initial;
            }
          }
        }

        .jumpToSection {
          color: $textColor3;
          font-size: 11px;
          cursor: pointer;

          &:hover {
            color: $textColor1;
          }
        }

        .jumpToSectionButton {
          width: fit-content;
        }
      }
    }

    .content {
      flex: 1;
    }
  }

  .marketsContainer {
    padding: 64px;
    font-size: 12px;

    .header {
      display: flex;
      justify-content: space-between;
      align-items: center;

      .tabs {
        display: flex;

        .tab {
          color: $textColor3;
          background-color: transparent;
          padding: 10px 20px;
          z-index: 99;
          border-radius: 4px 4px 0 0;
          font-size: 14px;
          cursor: pointer;

          &:hover {
            background-color: rgba(0, 0, 0, 0.025);
          }

          &.active {
            color: $primaryColor;
            font-weight: 600;
            border-bottom: 2px solid $primaryColor;

            &.primaryColor2 {
              color: $primaryColor2;
              border-color: $primaryColor2;
            }
            &.primaryColor3 {
              color: $primaryColor3;
              border-color: $primaryColor3;
            }
          }
        }
      }
    }

    .tableContainer {
      background-color: $white;
      padding: 32px;
      border-radius: 4px 0 4px 4px;
      overflow: hidden;
      @include boxShadow15b;

      &:hover {
        @include boxShadow30b;
      }

      table {
        width: 100%;
        border-collapse: collapse;

        thead {
          tr {
            th {
              padding-bottom: 12px;
              text-align: right;
              white-space: nowrap;
              font-weight: 300;
            }
          }
        }

        tbody {
          tr {
            // &:hover {
            //   background-color: rgba(0, 0, 0, 0.03);
            // }

            td {
              padding: 12px 0;

              &:last-child {
                width: 1px;
              }

              .cellContainer {
                display: flex;

                .coinLogo {
                  width: 32px;
                  margin-left: 8px;
                }

                .description {
                  display: flex;
                  flex-direction: column;

                  .title {
                    font-size: 14px;
                    font-weight: 600;
                    white-space: nowrap;

                    .currencyName {
                      font-weight: normal;

                      &.currencyFontWeight2 {
                        font-weight: bold;
                      }
                    }
                  }

                  .subTitle {
                    color: $textColorFade;
                    text-decoration: none;
                    font-size: 11px;
                    white-space: nowrap;
                  }
                }

                .priceChanges {
                  color: $white;
                  background-color: #f84960;
                  display: flex;
                  justify-content: center;
                  align-items: center;
                  border-radius: 10px;
                  direction: ltr;
                  height: 30px;
                  width: 80px;

                  &.positive {
                    background-color: #02c076;
                  }

                  &.coinPriceChangesColor1 {
                    background-color: #f84960;

                    &.positive {
                      background-color: #02c076;
                    }
                  }
                  &.coinPriceChangesColor2 {
                    background-color: #ff788a;

                    &.positive {
                      background-color: #51cf9e;
                    }
                  }
                }

                .priceChart {
                  height: 50px;
                }
              }
            }
          }
        }
      }
    }

    .footer {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      height: 43.33px;

      .more {
        color: $textColor2;
        cursor: pointer;

        &:hover {
          color: $textColor1;
        }
      }
    }
  }

  .promotionContainer {
    margin: 0 64px 32px;
    overflow: hidden;

    .slider {
      display: flex;
      width: calc(100vw - 128px);
      overflow-x: scroll;
      -ms-overflow-style: none; /* IE and Edge */
      scrollbar-width: none; /* Firefox */
      cursor: grab;

      &::-webkit-scrollbar {
        display: none; /* Chrome, Safari and Opera */
      }

      .promotion {
        display: inline-block;
        height: 160px;
        min-width: 280px;
        margin-left: 16px;
        overflow: hidden;

        img {
          height: 100%;
          width: 100%;
          border-radius: 4px;
        }
      }
    }
  }

  .exchangeContainer {
    display: flex;
    padding: 64px;
    font-size: 12px;

    .description {
      flex: 1;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;

      .texts {
        text-align: right;

        h2 {
          margin-bottom: 8px;
          font-size: 26px;
          font-weight: 500;
        }

        h3 {
          color: $textColor2;
          margin-bottom: 16px;
          font-size: 16px;
          font-weight: 500;
        }

        .imageContainer {
          width: 200px;
          height: 200px;
        }
      }
    }

    .content {
      flex: 1;
      display: flex;
      justify-content: center;

      .calculatorContainer {
        background-color: $white;
        width: 50%;
        max-width: 320px;
        border-radius: 4px;
        padding: 16px;
        @include boxShadow30b;

        form {
          display: flex;
          flex-direction: column;
          align-items: center;

          .picker {
            margin-bottom: 12px;
            width: 100%;
          }

          .inputContainer {
            position: relative;
            margin-bottom: 12px;
            width: 100%;

            input {
              color: #3e4548;
              background-color: $white;
              display: flex;
              padding: 10px 16px;
              width: calc(100% - 34px);
              position: relative;
              border: 1px solid #e6e8ec;
              border-radius: 4px;
              font-size: 14px;
              direction: ltr;
            }

            label {
              color: $textColor3;
              position: absolute;
              top: 15.665px;
              right: 16px;
            }
          }

          .button {
            top: initial;
            transition: initial;
            width: calc(100% - 32px);
            margin-top: 16px;
            text-align: center;
          }

          .reverse {
            width: 24px;
            height: 24px;
            margin: 8px 0 16px;
            filter: invert(70%) sepia(81%) saturate(0%) hue-rotate(85deg)
              brightness(101%) contrast(88%);
            cursor: pointer;

            &:hover {
              filter: none;
            }
          }
        }
      }
    }
  }

  .informationContainer {
    display: flex;
    padding: 0 64px;
    font-size: 12px;
    min-height: calc(100vh - 300px);

    &:nth-child(even) {
      flex-direction: row-reverse;
    }

    .context {
      flex: 1;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;

      h2 {
        margin-bottom: 8px;
        font-size: 30px;
        font-weight: 500;
      }

      h3 {
        color: $textColor2;
        margin-bottom: 16px;
        font-size: 16px;
        font-weight: 500;
      }
    }

    .image {
      flex: 1;
      display: flex;
      justify-content: center;
      align-items: center;

      .imageContainer {
        width: 50%;
      }
    }
  }

  .unfinished {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 300px;
    text-align: center;
    font-size: 12px;
  }

  footer {
    color: $white;
    background-color: #535353;
    display: flex;
    flex-direction: column;
    font-size: 11px;
    padding: 32px 64px;

    .content {
      display: flex;

      .section {
        flex: 1;
        padding: 32px;

        &:first-child {
          padding-right: 0;
        }
        &:last-child {
          padding-left: 0;
        }

        &.wide {
          flex: 3;
        }

        h3 {
          text-align: right;
          font-size: 14px;
          margin: 0 auto 8px;
        }

        p {
          text-align: justify;
        }

        ul {
          list-style-type: none;
          padding-right: 0;

          li {
            margin-bottom: 4px;
          }
        }

        a {
          color: $white;
          text-decoration: none;
        }
      }
    }

    .footNote {
      display: flex;
      justify-content: space-between;
      margin-top: 32px;

      .policy {
      }

      .socialMedias {
        display: flex;

        a {
          display: flex;
          align-items: flex-end;

          img {
            position: relative;
            top: 0;
            width: 16px;
            height: 16px;
            margin-right: 16px;
            filter: invert(100%) sepia(0%) saturate(0%) hue-rotate(99deg)
              brightness(103%) contrast(105%);
            cursor: pointer;

            &:hover {
              top: -2px;
              transition: top 0.2s;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1024px) {
  .container {
    .exchangeContainer {
      .content {
        .calculatorContainer {
          width: 66%;
        }
      }
    }

    .informationContainer {
      .image {
        .imageContainer {
          width: 66%;
        }
      }
    }
  }
}

@media screen and (max-width: 960px) {
  .container {
    .marketsContainer {
      .tableContainer {
        table {
          thead {
            tr {
              th {
                &:nth-last-child(2) {
                  display: none;
                }
              }
            }
          }
          tbody {
            tr {
              td {
                &:nth-last-child(2) {
                  display: none;
                }
              }
            }
          }
        }
      }
    }

    footer {
      color: $white;
      background-color: #535353;
      display: flex;
      flex-direction: column;
      font-size: 11px;
      padding: 32px 64px;

      .content {
        display: flex;

        .section {
          &:not(.wide) {
            display: none;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 860px) {
  .container {
    .heading {
      flex-direction: column-reverse;
      padding: 0 32px;

      &.mobileFirstViewImage2 {
        min-height: initial;

        .content {
          display: none;
        }
      }

      .title {
        justify-content: flex-start;

        .header {
          display: flex;
          flex-direction: column;
          align-items: center;
          text-align: center;

          .inputGroup {
            justify-content: center;
          }
        }
      }
    }

    .marketsContainer {
      padding: 32px;
    }

    .promotionContainer {
      margin: 0 32px 32px;

      .slider {
        width: calc(100vw - 64px);
      }
    }

    .exchangeContainer {
      flex-direction: column;
      padding: 32px;

      .description {
        .texts {
          display: flex;
          flex-direction: column;
          align-items: center;
          text-align: center;
          margin-bottom: 24px;
        }
      }

      .content {
        .calculatorContainer {
          width: 100%;
        }
      }
    }

    .informationContainer {
      flex-direction: column-reverse;
      min-height: initial;
      margin-bottom: 64px;
      text-align: center;

      &:last-child() {
        margin-bottom: 0;
      }

      &:nth-child(even) {
        flex-direction: column-reverse;
      }
    }

    footer {
      padding: 32px;

      .content {
        .section {
          padding: 0 32px;

          &:first-child {
            padding-right: 0;
          }
          &:last-child {
            padding-left: 0;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 780px) {
  .container {
    .heading {
      .title {
        .header {
          .jumpToSection {
            font-size: 12px;
          }
        }
      }

      .content {
        flex: 1;
      }
    }

    .marketsContainer {
      .tableContainer {
        padding: 32px 16px;

        table {
          thead {
            tr {
              th {
                &:last-child {
                  display: none;
                }
              }
            }
          }
          tbody {
            tr {
              td {
                &:last-child {
                  display: none;
                }
                &:nth-last-child(3) {
                  width: 1px;
                }

                .cellContainer {
                  .priceChanges {
                    width: fit-content;
                    padding: 0 12px;
                  }

                  .description {
                    .title {
                      font-size: 12px;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }

    .exchangeContainer {
      .content {
        .calculatorContainer {
          max-width: initial;
        }
      }
    }

    footer {
      font-size: 12px;
      padding: 0 32px;

      .content {
        flex-direction: column;

        .section {
          &:first-child {
            padding: 32px 0;
          }
          &:last-child {
            padding: 32px 0;
          }
        }
      }

      .footNote {
        padding: 0 0 32px;
      }
    }
  }
}
