.container {
  background-color: lightgray;
  padding: 32px 64px;
  font-size: 12px;
  position: fixed;
  top: 0;
  left: 0;
  height: calc(100vh - 64px);
  z-index: 999999;
  overflow-y: scroll;

  .header {
    display: flex;
    align-items: center;
    cursor: pointer;

    img {
      width: 8px;
      margin-left: 8px;
    }

    .notice {
      font-size: 11px;
      margin-right: 8px;
    }
  }

  hr {
    margin-bottom: 16px;
  }

  .content {
    display: flex;
    flex-wrap: wrap;

    .section {
      flex: 1 0 26%;
      max-width: 33.33%;
      margin-bottom: 32px;

      .title {
        margin-bottom: 8px;
      }

      .options {
        display: flex;
        flex-direction: column;

        .option {
          display: flex;
          align-items: center;
          margin-bottom: 4px;
          cursor: pointer;
        }

        input[type='text'] {
          width: 66%;
        }
      }
    }
  }

  .footer {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 32px;

    &:last-child {
      margin-bottom: 0;
    }

    button {
      cursor: pointer;
      margin-right: 16px;
    }
  }
}

@media screen and (max-width: 860px) {
  .container {
    padding: 32px;

    .content {
      .section {
        flex: 1 0 51%;
        max-width: 50%;
      }
    }
  }
}

@media screen and (max-width: 640px) {
  .container {
    .header {
      .notice {
        font-size: 9px;
      }
    }
    .content {
      .section {
        flex: 1 0 100%;
        max-width: 100%;
      }
    }
  }
}
