.container {
  position: relative;
  overflow: hidden;
  transition: background 400ms;
  -webkit-tap-highlight-color: transparent;

  span.ripple {
    position: absolute;
    border-radius: 50%;
    transform: scale(0);
    animation: ripple 600ms linear;
    background-color: rgba(225, 225, 225, 0.7);
  }
}

@keyframes ripple {
  to {
    transform: scale(4);
    opacity: 0;
  }
}
