@import 'styles/variables';

$bg-disabled-color: $gray;
$bg-enabled-color: rgba($primaryColor, 0.5);
$lever-disabled-color: $white;
$lever-enabled-color: $primaryColor;

.container {
  display: flex;
  flex-direction: row-reverse;
  position: relative;
  font-size: 12px;
  line-height: 26px;
  cursor: pointer;

  input {
    display: none;

    &:checked + .label {
      color: $textColor1;

      &:before {
        background-color: #0000a5;
      }

      &:after {
        content: url(../../assets/images/moon.svg);
        top: -9px;
        right: -4px;
      }
    }
  }

  // unchecked
  .label {
    color: $textColorFade;
    display: block;
    padding: 0 44px 0 0;
    cursor: pointer;

    &:before {
      content: '';
      position: absolute;
      top: -6px;
      right: 0;
      width: 36px;
      height: 14px;
      background-color: #9a9a9a;
      border-radius: 14px;
      z-index: 1;
      transition: background-color 0.28s cubic-bezier(0.4, 0, 0.2, 1);
    }

    &:after {
      content: url(../../assets/images/sun.svg);
      position: absolute;
      top: -11px;
      right: 17px;
      width: 20px;
      height: 20px;
      //   background-color: $lever-disabled-color;
      border-radius: 14px;
      //   box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
      //     0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
      z-index: 2;
      transition: all 0.28s cubic-bezier(0.4, 0, 0.2, 1);
      transition-property: right, background-color;
    }
  }
}
