@import 'styles/variables';
@import 'styles/mixins';

html[data-theme='dark'] {
  .container {
    .picker {
      color: #3e4548;
      background-color: lightgray;
    }

    ul {
      background-color: lightgray;
    }
  }
}

.container {
  &:focus {
    outline: none;

    .picker {
      border-color: #101010;
      border-width: 1px;

      .selected {
        // padding: 15px 13px;
      }

      .actions {
        .action {
          //   width: 51.33px;
          //   height: 51.33px;
        }
      }
    }
  }

  .label {
    color: #23262f;
    padding: 0;
    margin: 0;
    font-size: 12px;
  }

  .picker {
    color: #3e4548;
    background-color: white;
    display: flex;
    position: relative;
    border: 1px solid #e6e8ec;
    border-radius: 4px;
    font-size: 14px;
    max-height: 43.33px;

    .selected {
      flex: 1;
      display: flex;
      align-items: center;
      padding: 10px 14px;

      img {
        width: 20px;
        height: 20px;
        margin-left: 8px;
      }
    }

    .actions {
      display: flex;
      flex-direction: row-reverse;

      .action {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 41.33px;
        cursor: pointer;

        &:hover {
          img {
            filter: none;
          }
        }

        img {
          width: 12px;
          height: 12px;
          filter: invert(72%) sepia(91%) saturate(0%) hue-rotate(173deg)
            brightness(99%) contrast(85%);
        }
      }

      .separator {
        align-self: stretch;
        background-color: #ccc;
        margin-bottom: 8px;
        margin-top: 8px;
        width: 1px;
        box-sizing: border-box;
      }
    }

    ul {
      background-color: white;
      list-style-type: none;
      margin: 0;
      padding: 4px 0;
      position: absolute;
      top: 50px;
      min-width: 100%;
      width: fit-content;
      max-width: 300px;
      max-height: 200px;
      overflow-y: scroll;
      border-radius: 4px;
      z-index: 999;
      @include boxShadow15b;

      &.empty {
        overflow-y: initial;
      }

      li {
        display: flex;
        align-items: center;
        height: 40px;
        padding: 0 10px;
        margin: 0 1px;
        white-space: nowrap;

        &:not(.disabled) {
          cursor: pointer;

          &:hover {
            background-color: rgba(0, 0, 0, 0.03);
          }
        }

        img {
          width: 20px;
          height: 20px;
          margin-left: 8px;
        }
      }

      .search {
        padding: 12px 14px;
        margin: 10.5px 14.5px;
      }
    }
  }
}
